.working-profiles {
  padding: 70px 0;
  overflow: hidden;
  &__desctop {
    @include r(767) {
      display: none; } }
  .swiper {
    position: relative;
    display: none;
    @include r(767) {
      display: block; }
    &:before {
      content: '';
      position: absolute;
      left: -15px;
      top: 0;
      height: 100%;
      width: 90px;
      background-image: linear-gradient(to left, rgba(255, 255, 255, 0), $white);
      z-index: 10; }
    &:after {
      content: '';
      position: absolute;
      right: -15px;
      top: 0;
      height: 100%;
      width: 90px;
      background-image: linear-gradient(to right, rgba(255, 255, 255, 0), $white);
      z-index: 10; }
    &-container {
      overflow: visible;
      padding: 0 40px;
      @include r(550) {
        padding: 0 120px; }
      @include r(450) {
        padding: 0 90px; }
      @include r(380) {
        padding: 0 60px; } }
    &-slide {
      @include center; } }
  &__block {
    margin-top: 70px;
    display: block;
    @include r(767) {
      width: 180px;
      @include center-column; }
    &:hover {
      p {
        color: darken($dark-khaki, 20%); }
      .working-profiles__block__img {
        &:before {
          opacity: 1; } } }
    &__img {
      position: relative;
      @include center;
      margin-bottom: 20px;
      &:before {
        content: '';
        width: 160px;
        height: 160px;
        background: #F6EFE5;
        position: absolute;
        top: -10px;
        border-radius: 100%;
        z-index: -1;
        opacity: 0;
        transition: all 0.3s; } }
    img {
      height: 140px; }
    p {
      font-family: 'SF Pro Display';
      font-size: 16px;
      line-height: 1.25;
      text-align: center;
      color: $dark-khaki;
      transition: all 0.3s; } } }
